
#show-menu {
  float: left;
  width: 8%;
  height: 100%;
  background: #ffffff;
}
.menu-cont {
  position: relative;
  width: 90%;
  height: 99%;
  margin: 0 auto;
  padding-top: 1rem;
  box-sizing: border-box;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.logo, .file, .download-file, .search, .save-resume {
  width: 100%;
  color: #42b983;
  text-align: center;
  font-size: 1.2rem;
  margin-bottom: 2rem;
}
.logo a {
  color: #42b983;
}
.file-des, .search-des {
  font-size: 1.6rem;
}

/*.download-file {*/
  /*position: absolute;*/
  /*bottom: 2rem;*/
/*}*/
.logo, .file, .download-file:hover {
  cursor: pointer;
}
.save-bar {
  position: absolute;
  z-index: 9999;
  top: 30px;
  left: 110%;
  width: 50rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 10px;
  background: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.save-bar input {
  width: 400px;
  height: 40px;
  border: solid 1px #f2f2f2;
  border-radius: 5px;
  padding: 3px;
}
.save-bar > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 10px;
}
.save-bar > div > label {
  width: 60px;
}
.action > label {
  display: inline-block;
  width: 60px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border: solid 1px #f2f2f2;
  border-radius: 10px;
  color: #ffffff;
}
.action > label:first-child {
  background: #3ca777;
  margin-left: 60px;
  margin-right: 5px;
}
.action > label:last-child {
  color: #313131;
}
.search-bar {
  position: absolute;
  z-index: 9999;
  top: 100px;
  left: 110%;
  width: 50rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 10px;
  background: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.search-bar > div > label {
  width: 60px;
}
.search-bar > div > input {
  width: 400px;
  height: 40px;
  border: solid 1px #f2f2f2;
  border-radius: 5px;
  padding: 3px;
  margin-bottom: 8px;
}
.download-choice {
  position: absolute;
  z-index: 9999;
  top: 18.5rem;
  left: 110%;
  width: 40rem;
  text-align: center;
  padding: 20px 10px;
  background: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.download-choice > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.download-choice > div > span {
  width: 60px;
  color: #000;
  font-weight: bolder;
  font-size: 14px;
}
.download-choice .name > input {
  width: 300px;
  height: 40px;
  border: solid 1px #f2f2f2;
  border-radius: 5px;
  padding: 3px;
  margin-bottom: 8px;
}
.download-choice .save > label {
  display: inline-block;
  width: 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border: solid 1px #f2f2f2;
  border-radius: 10px;
  color: #ffffff;
  background: #3ca777;
  margin-left: 60px;
}
.size-choice {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
}
.setdpi {
  display: inline-block;
  padding: 1rem;
  font-size: 1.6rem;
  color: #3ca777;
}
.custom {
  display: inline-block;
  width: 10rem;
  font-size: 1.6rem;
  color: #3ca777;
  margin-top: 12px;
}
.custom input {
  width: 4.5rem;
  text-align: right;
  margin-left: 5px;
  border: solid 1px #e3e3e3;
  border-radius: 3px;
}
.ok-choice {
  display: inline-block;
  text-align: center;
  border: 1px solid #4cd195;
  border-radius: 6px;
  padding: 1px 8px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.ok-choice:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0);
  cursor: pointer;
}



